"use client";

import React, { useRef } from "react";
import Image from "next/image";

const izq = "/icons/izquierdaC.svg";
const der = "/icons/derechaC.svg";
const coraR = "/icons/coraR.svg";
const star = "/icons/star 5.svg";
const pic4 = "/pic4.jpg";
const pic5 = "/pic5.jpg";
const pic6 = "/pic6.jpg";

const juegos = [
  { title: "Cat Quest II", image: pic4, tags: ["RPG"], rating: 5.0 },
  { title: "Cat Quest II", image: pic4, tags: ["RPG"], rating: 3.0 },
  { title: "Cat Quest II", image: pic4, tags: ["RPG"], rating: 3.5 },
  { title: "Arcadegeddon", image: pic5, tags: ["AVENTURA", "RPG"], rating: 4.5 },
  { title: "River City Girls", image: pic6, tags: ["RPG"], rating: 5.0 },
];

const JuegosGratis = () => {
  const scrollRef = useRef<HTMLDivElement>(null);

  const scroll = (dir: "left" | "right") => {
    if (scrollRef.current) {
      const containerWidth = scrollRef.current.clientWidth;
      const scrollAmount = containerWidth; // scroll exacto ancho visible (3 tarjetas)
      scrollRef.current.scrollBy({
        left: dir === "left" ? -scrollAmount : scrollAmount,
        behavior: "smooth",
      });
    }
  };

  return (
    <div className="w-full bg-deep rounded-3xl text-white py-10 px-6 my-6 overflow-hidden">
      <div className="max-w-7xl mx-auto relative">
        {/* Header */}
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-xl font-primary">Juegos gratis</h2>
          <div className="flex items-center gap-2">
            <a href="#" className="text-primary text-xl hover:underline">
              Ver todos
            </a>
            <button onClick={() => scroll("left")}>
              <Image src={izq} alt="izquierda" width={40} height={40} />
            </button>
            <button onClick={() => scroll("right")}>
              <Image src={der} alt="derecha" width={40} height={40} />
            </button>
          </div>
        </div>

        {/* Carrusel */}
        <div
          ref={scrollRef}
          className="flex gap-6 overflow-x-auto scroll-smooth no-scrollbar w-full"
          style={{ scrollPaddingLeft: 24 }}
        >
          {juegos.map((juego, i) => (
            <div
              key={i}
              className="flex-shrink-0 w-[calc((100%/3)-16px)] bg-deep rounded-xl overflow-hidden shadow-md relative"
            >
              {/* Imagen con Ã­cono */}
              <div className="relative">
                <Image
                  src={juego.image}
                  alt={juego.title}
                  width={400} // ancho natural mayor para que se adapte bien
                  height={240}
                  className="rounded-4xl object-cover w-full h-[240px]"
                  priority={i === 0}
                />
                <div className="absolute top-2 right-2">
                  <Image src={coraR} alt="heart" width={56} height={56} />
                </div>
              </div>

              {/* Contenido */}
              <div className="p-4">
                <div className="flex gap-2 mb-2">
                  {juego.tags.map((tag, j) => (
                    <span
                      key={j}
                      className="bg-categorico text-xs px-2 py-1 rounded-md text-white"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
                <div className="flex justify-between items-center mb-2">
                  <h3 className="text-base font-semibold">{juego.title}</h3>
                  <div className="flex items-center gap-1">
                    {[...Array(5)].map((_, i) => (
                      <Image
                        key={i}
                        src={star}
                        alt="estrella"
                        width={14}
                        height={14}
                      />
                    ))}
                    <span className="text-xs font-medium">
                      {juego.rating.toFixed(1)}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default JuegosGratis;
